<template>
  <div class="invest">
    <div id="investCharts">

    </div>
  </div>
</template>

<script>
  //ecahrts
  import echarts from 'echarts'
  import Vue from "vue"

  Vue.prototype.$echarts = echarts

  export default {
    name: "AmFoundInvest",
    methods: {
      initData() {
        let myChart = this.$echarts.init(document.getElementById('investCharts'))
        // 
        myChart.setOption(
          {
            title : {
              text: '2017',
              x:'center'
            },
            tooltip : {
              trigger: 'item'
            },
            legend: {
              orient: 'vertical',
              x:'left',
              data:['']
            },
            dataRange: {
              min: 0,
              max: 2500,
              x: 'left',
              y: 'bottom',
              text:['',''],           // 
              calculable : true
            },
            toolbox: {
              show: true,
              orient : 'vertical',
              x: 'right',
              y: 'center',
              feature : {
                mark : {show: true},
                dataView : {show: true, readOnly: false},
                restore : {show: true},
                saveAsImage : {show: true}
              }
            },
            roamController: {
              show: true,
              x: 'right',
              mapTypeControl: {
                'china': true
              }
            },
            series : [
              {
                name: '',
                type: 'map',
                mapType: 'china',
                roam: false,
                itemStyle:{
                  normal:{label:{show:true}},
                  emphasis:{label:{show:true}}
                },
                data:[
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)},
                  {name: '',value: Math.round(Math.random()*1000)}
                ]
              }
            ]

          }

        )

      }
    },
    mounted() {
      this.initData();
    }
  }
</script>

<style lang="scss" scoped>
  .invest {
    flex: 1;
    height: 85%;
    padding:  1% 3%;
    #investCharts {
      box-shadow: 0 0 10px #a6e3e9;
      border-radius: 10px;
      width: 100%;
      height: 100%;
    }
  }

</style>



