<template>
  <div class="fund-flow-table">
    <el-table
      :data="flowData"
      style="width: 100%"
      align='center'
      @sort-change='handleSortChange'>
      <el-table-column
        type="index"
        label=""
        align='center'
        width="80">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label=""
        align='center'
        width="220"
        sortable='custom'>
        <template slot-scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="incomePayType"
        label=""
        align='center'
        width="180"
        :formatter="formatterType"
        :filters="fields.incomePayType.filter.list"
        :filter-method="filterType"
        >
        <template slot-scope="scope">
          <el-tag style="margin-left: 10px" >{{ scope.row.incomePayType }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="incomePayDes"
        label=""
        align='center'
        width="240">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.incomePayDes }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="income"
        label=""
        align='center'
        width="170"
        sortable='custom'>
        <template slot-scope="scope">
          <span style="color:#00d053">+ {{ scope.row.income }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="pay"
        label=""
        align='center'
        width="170"
        sortable='custom'>
        <template slot-scope="scope">
          <span style="color:#f56767">- {{ scope.row.pay }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="accoutCash"
        label=""
        align='center'
        width="170"
        sortable='custom'>
        <template slot-scope="scope">
          <span style="color:#4db3ff">{{ scope.row.accoutCash }}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="remarks"
        label=""
        align='center'
        width="250">
        <template slot-scope="scope">
          <span>{{ scope.row.remarks}}</span>
        </template>
      </el-table-column>
      <el-table-column
        prop="operation"
        align='center'
        label=""
        fixed="right"
        width="180">
        <template slot-scope='scope'>
          <el-button
            type="warning"
            icon='edit'
            size="mini"
            @click="dialogFormVisible = true ,handleEdit(scope.$index, scope.row)"
          ></el-button>

          <el-dialog
            title=""
            :visible.sync="dialogFormVisible"
            :modal-append-to-body="false"
            :close-on-click-modal="false"
             class="edit-form"
            :before-close="handleClose">
            <el-form :model="form"  ref="form" class="flowForm">
              <el-form-item label=":" :label-width="formLabelWidth" prop="incomePayType">
                <el-select v-model="form.incomePayType" placeholder="">
                  <el-option label="" value="0"></el-option>
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                  <el-option label="" value="3"></el-option>
                  <el-option label="" value="4"></el-option>
                  <el-option label="" value="5"></el-option>
                  <el-option label="" value="6"></el-option>
                  <el-option label="" value="7"></el-option>
                  <el-option label="" value="8"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label=":" :label-width="formLabelWidth" prop="incomePayDes">
                <el-input type="textarea" v-model="form.incomePayDes" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label=":" :label-width="formLabelWidth" prop="income">
                <el-input v-model="form.income" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label=":" :label-width="formLabelWidth" prop="pay">
                <el-input v-model="form.pay" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label=":" :label-width="formLabelWidth" prop="accoutCash">
                <el-input v-model="form.accoutCash" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label=":" :label-width="formLabelWidth" prop="remarks">
                <el-input type="textarea" v-model="form.remarks" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button @click.native="handleCancel('editForm')"></el-button>
                <el-button type="primary" @click.native="handleUpdate('editForm')"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>

          <el-button
            type="danger"
            icon='delete'
            size="mini"
            @click="handleDelete(scope.$index, scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from "axios"
  import url from  "@/apis/url.js"
  export default {
    name: "FundFlowTable",
    props:{
      flowDataa:Array
    },
    data() {
      return {
        dialogTableVisible: false,
        dialogFormVisible: false,

        //form
        form: {
          incomePayType: "",
          incomePayDes:"",
          income:"",
          pay:"",
          accoutCash:"",
          remarks:""
        },

        formLabelWidth: '120px',
        flowData:[],
        //
        fields: {
          incomePayType:{
            filter: {
              list: [{
                text: '',
                value: 0
              },{
                text: '',
                value: 1
              }, {
                text: '',
                value: 2
              }, {
                text: '',
                value: 3
              }, {
                text: '',
                value: 4
              }, {
                text: '',
                value: 5
              }, {
                text: '',
                value: 6
              }, {
                text: '',
                value: 7
              }, {
                text: '',
                value: 8
              }],
              multiple: true
            }
          },
          create_time: {
            info: {
              prop: 'create_time',
              label: '',
              sortable: true
            },
            filter: {

            },
            style: {
              width: '260',
              align: 'center'
            }
          },
        },
        format_type_list: {
          0: '',
          1: '',
          2: '',
          3: '',
          4: '',
          5: '',
          6: '',
          7: '',
          8: ''
        },
        //
        paginations: {
          pageIndex: 1,  // 
          total: 231,        // 
          page_size: 20,   // 1
          page_sizes: [5, 10, 15, 20],  //
        }
      }
    },
    methods: {
      // let item = this.flowData

      formatterType(value,row) {
        // const type = parseInt(item.incomePayType);
        // return this.format_type_list[type];
        // return row.incomePayType == value
        this.flowData.forEach(ele=>{
          // console.log(this.flowData)
          if(ele.incomePayType=="0"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="1"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="2"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="3"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="4"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="5"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="6"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="7"){
            ele.incomePayType=""
          }else if(ele.incomePayType=="8"){
            ele.incomePayType=""
          }
        })
      },
      //
        filterType(val) {
        let filterTypeValue = val;
        this.aaaa.push(filterTypeValue)
          console.log(this.aaaa)
      },

      getFlowInfo(){
        axios.get(url.url+"/getFundflow?pageIndex=1")
          .then(this.getFlowInfoSucc)
      },
      getFlowInfoSucc(res){
        res = res.data
        // console.log(res)
        if(res.ok==1){
          this.flowData = res.data
          // console.log(this.flowData)
          this.formatterType()
        }else{
          console.log("")
        }
      },
      //pageSize
      handleSizeChange(val) {
        // console.log(` ${val} `);
      },
      //
      handleCurrentChange(val) {
        // console.log(`: ${val}`);
        this.paginations.pageIndex = val
      },
      //
      handleSortChange(column, prop, order){
        // console.log(column.prop);
        let name = 0
        let num = 1
        if(column.prop == "createTime"){
          name = 0
        }else if(column.prop == "income"){
          name = 1
        }else if(column.prop == "pay"){
          name = 2
        }else if(column.prop == "accoutCash"){
          name = 3
        }

        if(column.order == "ascending"){
          num = 1
        }else if(column.order == "descending"){
          num = -1
        }

        axios({
          methods: "get",
          params:{
            name:name,
            num:num
          },
          url:url.url+"/getFundflowOrder"
        }).then((res)=>{
          console.log(res.data)
          this.flowData = res.data.data
          this.formatterType()
        }).catch((err)=>{
          console.log("")
        })

        // console.log(column.order);
      },

      //
      handleEdit(index, row) {
        console.log(index, row);
        this.dialogFormVisible = true;
        this.form = Object.assign({}, row);
      },
      handleClose(done) {
        /*done();
        location.reload();*/
        this.editFormVisible = false;
      },

      handleCancel(formName) {
        this.dialogFormVisible = false;
      },


      //
      handleDelete(index, row) {
        let rowData = row
        let deleteId = rowData._id
        // console.log(deleteId)
        axios({
          methods: "get",
          params:{
            id:deleteId
          },
          url:url.url+"/delFundflow"
        }).then((res)=>{
          // console.log(res.data)
          alert(res.data.msg)
          if(res.data.ok == 1){
            this.getFlowInfo()
          }
          // this.flowData = res.data.data
        }).catch((err)=>{
          console.log("")
        })
      }
    },
    mounted(){
      this.getFlowInfo()
    },
    watch:{
      flowDataa(e) {
          this.flowData = this.flowDataa;
          this.formatterType()
      }
    }
  }
</script>

<style scoped>
  /*.el-dialog>>>{*/
    /*overflow: auto;*/
  /*}*/
</style>



