<template>
    <div class="AmModifyInfo">
        <div class="content">
            <el-row :gutter="10">
                <el-col :md="8">
                    <div class="grid-content modifyInfo">
                        <div class="modifyTitle">
                            <img src="/static/img/modify.jpg" alt=""/>
                            <p></p>
                        </div>
                        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                            <el-form-item label="" prop="username">
                                <el-input placeholder="" v-model="ruleForm.username" disabled></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="nickname">
                                <el-input v-model="ruleForm.nickname" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="touziyear">
                                <el-select v-model="ruleForm.touziyear" placeholder="">
                                    <el-option label="1" value="1"></el-option>
                                    <el-option label="2" value="2"></el-option>
                                    <el-option label="3" value="3"></el-option>
                                    <el-option label="4" value="4"></el-option>
                                    <el-option label="5" value="5"></el-option>
                                    <el-option label="6" value="6"></el-option>
                                    <el-option label="7" value="7"></el-option>
                                    <el-option label="8" value="8"></el-option>
                                    <el-option label="9" value="9"></el-option>
                                    <el-option label="10" value="10"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" prop="email">
                                <el-input v-model="ruleForm.email" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="telphone" >
                                <el-input v-model="ruleForm.telphone" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitRuleForm('ruleForm')"></el-button>
                                <el-button @click="resetForm('ruleForm')"></el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>

                <el-col :md="8">
                    <div class="grid-content modifyInfo">
                        <div class="modifyTitle">
                            <img src="/static/img/modify.jpg" alt=""/>
                            <p></p>
                        </div>
                        <el-form :model="pwdForm" :rules="pwdRules" ref="pwdForm" label-width="100px" class="demo-ruleForm">
                            <el-form-item label="" prop="password">
                                <el-input type="password" v-model="pwdForm.password" placeholder="" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="newPass">
                                <el-input type="password" v-model="pwdForm.newPass" placeholder="" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="checkPass">
                                <el-input type="password" v-model="pwdForm.checkPass" placeholder="" autocomplete="off"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitPwdForm('pwdForm')"></el-button>
                                <el-button @click="resetForm('pwdForm')"></el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>

                <el-col :md="8">
                    <div class="grid-content modifyInfo">
                        <div class="modifyTitle">
                            <img src="/static/img/modify.jpg" alt=""/>
                            <p></p>
                        </div>
                        <el-form :model="phoneForm" :rules="phoneRules" ref="phoneForm" label-width="120px" class="demo-ruleForm">
                            <el-form-item label="" prop="phone" >
                                <el-input v-model="phoneForm.phone" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" prop="basicType">
                                <el-checkbox-group v-model="phoneForm.basicType" class="phoneGroup">
                                    <el-checkbox label="" name="basicType"></el-checkbox>
                                    <el-checkbox label="" name="basicType"></el-checkbox>
                                    <el-checkbox label="" name="basicType"></el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>
                            <el-form-item label="" prop="selectType">
                                <el-checkbox-group v-model="phoneForm.selectType" class="phoneGroup">
                                    <el-checkbox label="" name="selectType"></el-checkbox>
                                    <el-checkbox label="//" name="selectType"></el-checkbox>
                                    <el-checkbox label="" name="selectType"></el-checkbox>
                                    <el-checkbox label="" name="selectType"></el-checkbox>
                                    <el-checkbox label="" name="selectType"></el-checkbox>
                                </el-checkbox-group>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitPhoneForm('phoneForm')"></el-button>
                                <el-button @click="resetForm('phoneForm')"></el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    import url from "../../apis/url";
    import axios from "axios";

    export default {
        name: "AmModifyInfo",
        components:{
        },
        data() {
            let validateTelphone = (rule, value, callback) =>{
                if (value === '') {
                    callback(new Error(''));
                } else {
                    let phoneRegex = /^1[3|4|5|7|8]\d{9}$/
                    if (!phoneRegex.test(value)) {
                        callback(new Error(''));
                    }
                    callback();
                }
            };
            let validateNewPass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else {
                    if (this.pwdForm.checkPass !== '') {
                        this.$refs.pwdForm.validateField('checkPass');
                    }
                    callback();
                }
            };
            let validateCheckPass = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error(''));
                } else if (value !== this.pwdForm.newPass) {
                    callback(new Error('!'));
                } else {
                    callback();
                }
            };
            return {
                ruleForm: {
                    username: '',
                    nickname: '',
                    touziyear: '',
                    email:'',
                    telphone:''
                },
                pwdForm:{
                    password: '',
                    newPass: '',
                    checkPass: ''
                },
                phoneForm:{
                    phone:'',
                    basicType: [],
                    selectType:[]
                },
                rules: {
                    // username: [
                    //      {  message: '', trigger: 'blur' }
                    // ],
                    nickname: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
                    ],
                    touziyear: [
                        { required: true, message: '', trigger: 'change' }
                    ],
                    email:[
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'email', message: '', trigger: ['blur', 'change'] }
                    ],
                    telphone:[
                        { required: true, validator: validateTelphone, trigger: 'blur' }
                    ]
                },
                pwdRules: {
                    password: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    newPass: [
                        { required: true,validator: validateNewPass, trigger: 'blur' }
                    ],
                    checkPass: [
                        {required: true, validator: validateCheckPass, trigger: 'blur' }
                    ]
                },
                phoneRules:{
                    phone:[
                        { required: true, validator: validateTelphone, trigger: 'blur' }
                    ],
                    basicType: [
                        { type: 'array', required: true, message: '', trigger: 'change' }
                    ],
                    selectType: [
                        { type: 'array', required: true, message: '', trigger: 'change' }
                    ]
                }
            };
        },
        methods:{
            showMessage(type,message){
                this.$message({
                    type: type,
                    message: message
                });
            },
            submitRuleForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let key = sessionStorage.getItem("aimanage");
                        axios.post(url.url+"/users/upUser",{
                            key,
                            nickname:this.ruleForm.nickname,
                            touziyear:this.ruleForm.touziyear,
                            email: this.ruleForm.email,
                            telphone: this.ruleForm.telphone

                        }).then(response=>{
                            // console.log(response);
                            if(response.data.ok === 1){
                                this.showMessage('success','~');
                                // this.$router.push('/AmInfoList');
                            }else{
                                this.$message.error('')
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            submitPwdForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let key = sessionStorage.getItem("aimanage");
                        axios.post(url.url+"/users/upPass",{
                            key,
                            pass1:this.pwdForm.password,
                            pass2:this.pwdForm.newPass
                        }).then(response=>{
                                console.log(response);
                                if(response.data.ok === 1){
                                    this.showMessage('success','~');
                                    // this.$router.push('/AmInfoList');
                                }else if(response.data.ok === 0){
                                    this.showMessage('error','~');
                                }else if(response.data.ok === -2){
                                    this.showMessage('warning','key~');
                                }else{
                                    this.$message.error('')
                                }
                            })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            submitPhoneForm(formName){
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        let key = sessionStorage.getItem("aimanage");
                        axios.post(url.url+"/users/upFuWu",{
                            key,
                            telphone:this.phoneForm.phone,
                            baseType:this.phoneForm.basicType,
                            changeType:this.phoneForm.selectType
                        }).then(response=>{
                            // console.log(response);
                            if(response.data.ok === 1){
                                this.showMessage('success','~');
                                // this.$router.push('/AmInfoList');
                            }else if(response.data.ok === -2){
                                this.showMessage('warning','key~');
                            }else{
                                this.$message.error('')
                            }
                        })
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            resetForm(formName) {
                this.$refs[formName].resetFields();
            },
            getPersonalInfo(){
                let key = sessionStorage.getItem("aimanage");

                axios.get(url.url+"/getPersonalInfo",{
                    params:{
                        key
                    }
                })
                .then(res=>{
                    // console.log(res.data.data.personalInfo  )
                    let dataArr = res.data.data.personalInfo.user;
                    this.ruleForm.username = dataArr.username;
                    this.ruleForm.nickname = dataArr.nickname;
                    // console.log(this.ruleForm.nickname)
                    this.ruleForm.touziyear = dataArr.touziyear;
                    this.ruleForm.email = dataArr.email;
                    this.ruleForm.telphone = this.phoneForm.phone = dataArr.telphone;
                    // console.log(this.phoneForm.phone)
                    this.phoneForm.baseType = (dataArr.baseType);
                    this.phoneForm.changeType = (dataArr.changeType);
                })
            }
        },
        mounted() {
            this.getPersonalInfo();
        }
    }
</script>

<style scoped>
    /*content*/
    .content{
        padding: 10px 15px;
    }
    /*modifyInfo*/
    .modifyInfo{
        border: 1px solid #dfdfdf;
        font-size: 14px;
        padding: 10px;
        margin: 5px;
    }
    .modifyTitle{
        width: 100%;
        cursor: pointer;
        background-color: #3bc5ff;
        color: #fff;
        font-size: 14px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 0;
        margin-bottom: 22px;
    }
    .modifyTitle img{
        width: 14px;
        height: 11px;
        margin-right: 5px;
    }
    .phoneGroup{
        display: flex;
        flex-direction: column;
    }
</style>


